@import reset
@import mixin

body
    +setWH(100vw,100vh)
    user-select: none
    overflow-x: hidden
    overflow-y: hidden
    +bgColor(#ffffff)

canvas.a-canvas
    +ab
    +setWH(100%, 100%)
    +setPosition("TL",0,0)

div.a-enter-vr
    +hide

#container
    +ab
    +setWH(0,0)
    overflow: hidden
    
div#playButton
    +ab
    cursor: pointer
    @include respond-to(landscape)
        +setFont(2vw, #ffffff, 0)
        +setPosition("TR",2vw,2vw)
    @include respond-to(portrait)
        +setFont(6vw, #ffffff, 0)
        +setPosition("TR",6vw,6vw)

video
    +hide
    
// 字型
@font-face 
    font-family: 'BebasRegular'
    font-weight: normal
    src: url('../fonts/BebasRegular.woff') format("woff")
@font-face 
    font-family: 'BebasBold'
    font-weight: normal
    src: url('../fonts/BebasBold.woff') format("woff")
@font-face 
    font-family: 'NotoSansTC'
    font-weight: normal
    src: url('https://fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Regular.woff') format('woff')